<template>
  <div class="container" style="overflow-y: auto">
    <div class="row">
      <div
        v-for="(order, index) in uniqueOrders"
        :key="index"
        class="col-3 mb-3"
      >
        <div v-if="order.bnum === bnum" class="card mt-3" style="width: 18rem">
          <h5 class="card-title">이미지</h5>
          <div class="card-body">
            <div class="card" style="width: 16rem">
              <ul class="list-group list-group-flush">
                <li class="list-group-item">
                  주문일시: {{ formatDate(order.odate) }}
                </li>
                <li class="list-group-item">주문번호: {{ order.osnum }}</li>
                <li class="list-group-item">사업자번호: {{ order.bnum }}</li>
              </ul>
            </div>
            <p></p>
            <!-- <button type="button" @click="setOstatusMikey(order)">확인</button> -->

            <button type="button">상세보기</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "B_Ordcard",
  components: {},
  props: {
    bnum: {
      type: Number,
      required: true,
    },
  },
  data() {
    return {
      orderList: [], // 주문 상황 리스트 정보를 저장할 배열
    };
  },
  mounted() {
    this.fetchOrderList();
  },
  computed: {
    uniqueOrders() {
      // 해당 사업자번호와 일치하는 주문만 반환
      return this.orderList.filter((order) => order.bnum === this.bnum);
    },
  },

  methods: {
    formatDate(date) {
      const formattedDate = new Date(date);
      return formattedDate.toLocaleString();
    },
    async fetchOrderList() {
      try {
        const response = await axios.get("/business/B_Ordering");
        this.orderList = response.data.sort(
          (a, b) => new Date(b.odate) - new Date(a.odate)
        ); // 주문일시를 최신순으로 정렬
        console.log("Response:", response.data);
      } catch (error) {
        console.error("Error fetching orderList:", error);
      }
    },
  },
};
</script>
