<template>
  <div style="position: absolute; top: 10px; right: 10px">
    <button
      v-if="sstatus_mikey === 401"
      @click="confirmShopClosure(402, '영업을 종료하시겠습니까?')"
      class="status-button"
    >
      운영종료하기
    </button>
    <button
      v-if="sstatus_mikey === 402"
      @click="confirmShopClosure(401, '영업을 시작하시겠습니까?')"
      class="status-button"
    >
      운영시작하기
    </button>
    <!-- <span v-if="sstatus_mikey === 402">(영업종료)</span> -->
    <!-- 영업 중지 문구 -->
    <div v-if="sstatus_mikey === 402" class="alert alert-warning" role="alert">
      영업이 중지되었습니다.
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "B_ShopStatus",
  props: ["bnum"],
  data() {
    return {
      sstatus_mikey: null,
    };
  },
  created() {
    // 컴포넌트가 생성될 때 운영 상태 값을 가져옵니다.
    this.fetchShopStatus();
  },
  methods: {
    async fetchShopStatus() {
      try {
        console.log("bnum:", this.bnum); // bnum 값 확인
        // 서버에 POST 요청을 보내 운영 상태 값을 가져옵니다.
        const response = await axios.post("/ShopStatus", {
          bnum: this.bnum, // bnum을 서버로 전달합니다.
        });

        // 응답 데이터에서 운영 상태 값을 설정합니다.
        this.sstatus_mikey = response.data.sstatus_mikey;
        console.log("Shop Status Response:", response.data); // 응답 데이터 확인
      } catch (error) {
        console.error("운영 상태 값을 가져오는 중 오류 발생:", error);
      }
    },
    async confirmShopClosure(newStatus, message) {
      // 확인 메시지 표시
      if (confirm(message)) {
        console.log("새로운 상태:", newStatus); // 새로운 상태 값 확인
        try {
          // 서버에 POST 요청을 보내 상태 값을 업데이트합니다.
          const response = await axios.post("/ShopStatusUpdate", {
            sstatus_mikey: newStatus,
            bnum: this.bnum,
          });
          // 응답에서 sstatus_mikey를 가져옵니다.
          const updatedStatus = response.data.sstatus_mikey;
          console.log("setStatusMikey Response:", updatedStatus);
          // 상태 값 업데이트 후 다시 운영 상태 값을 가져옵니다.
          this.fetchShopStatus(); // 수정된 부분: 상태 업데이트 후 다시 상태를 가져오도록 변경
        } catch (error) {
          console.error("운영 상태 값을 업데이트하는 중 오류 발생:", error);
        }
      }
    },
  },
};
</script>

<style scoped>
.status-button {
  background-color: #4caf50; /* Green */
  border: none;
  color: white;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 8px;
}

.status-button:hover {
  background-color: #45a049;
}

.status-button:active {
  background-color: #3e8e41;
}
</style>