<template>
  <div>
    <div class="nav nav-tabs" id="nav-tab" role="tablist">
      <button
        v-for="(tab, index) in tabs"
        :key="index"
        :class="{ 'nav-link': true, active: index === activeTabIndex }"
        :id="'nav-' + tab.id + '-tab'"
        data-bs-toggle="tab"
        :data-bs-target="'#nav-' + tab.id"
        type="button"
        role="tab"
        :aria-controls="'nav-' + tab.id"
        :aria-selected="index === activeTabIndex"
        @click="changeTab(index)"
      >
        <router-link
          :to="{ path: '/business/B_Ordering', query: { status: tab.status } }"
        >
          {{ tab.name }}
        </router-link>
      </button>
    </div>

    <div class="tab-content" id="nav-tabContent">
      <!-- 각 탭 내용 -->
      <keep-alive>
        <div
          v-for="(tab, index) in tabs"
          :key="index"
          :id="'nav-' + tab.id"
          class="tab-pane fade"
          :class="{ 'show active': index === activeTabIndex }"
          :role="'tabpanel'"
          :aria-labelledby="'nav-' + tab.id + '-tab'"
        >
          <div class="container">
            <div class="row">
              <template v-if="tab.id === 'home'">
                <B_Ordcard :bnum="bnum" />
              </template>
              <template v-else>
                <B_OrdSpecCard :bnum="bnum" :orderStatus="tab.orderStatus" />
              </template>
            </div>
          </div>
        </div>
      </keep-alive>
      <B_ShopStatus :bnum="bnum" />
    </div>
  </div>
</template>

<script>

import B_OrdSpecCard from "@/components/business/MS_B_OrdSpecCard.vue";
import B_Ordcard from "@/components/business/MS_B_AllOrdcard.vue";
import B_ShopStatus from "@/components/business/MS_B_ShopStatus.vue";

export default {
  name: "B_Ordering",
  components: {
    B_OrdSpecCard,
    B_Ordcard,
    B_ShopStatus,
  },
  data() {
    return {
      tabs: [
        { id: "home", name: "전체", orderStatus: 800, status: 800 },
        {
          id: "OrderReady",
          name: "주문수락대기",
          orderStatus: 802,
          status: 802,
        },
        { id: "Preparing", name: "조리중", orderStatus: 803, status: 803 },
        { id: "Start", name: "배달시작", orderStatus: 804, status: 804 },
        { id: "contact", name: "배달완료", orderStatus: 805, status: 805 },
      ],
      activeTabIndex: 0,
      bnum: 1003, // 로그인한 사용자의 사업자 번호
      orderList: [],
    };
  },
  mounted() {
    const status = this.$route.query.status;
    if (status) {
      this.activeTabIndex = status - 1;
    }
  },
  methods: {
    changeTab(index) {
      this.activeTabIndex = index;
    },
  },
  computed: {
    filteredOrders() {
      const status = Number(this.$route.query.status);
      if (status === 800) {
        // status가 1이면 모든 주문을 반환
        return this.orderList;
      } else {
        // 그렇지 않으면 status에 맞는 주문만 필터링
        return this.orderList.filter(
          (order) =>
            order.ostatus_mikey === this.tabs[status - 1].orderStatus &&
            order.bnum === this.bnum
        );
      }
    },
  },
};
</script>

